import React, { Component } from 'react'
import R from 'ramda'
import Helmet from 'react-helmet'
import styled from 'styled-components'
import { rhythm } from '../utils/typography'
import { PageWrapper, PostContentWrapper } from '../components/content-wrapper'
import NavBar from '../components/NavBar'
import Footer from '../components/footer'
import TagsTreepmap from '../components/TagsTreepmap'

export type Tag = {
  name: string,
  count: number,
}

const Title = styled.h1`
  font-family: 'Roboto';
  font-size: 2.5rem;
  font-weight: 400;
  line-height: ${rhythm(2)};
  margin-top: ${rhythm(2)};
`

/**
 * 모든 태그 목록
 *
 * @class Tags
 * @extends {Component}
 */
class TagsRoute extends Component {
  tags: Array<Tag> = []

  UNSAFE_componentWillMount() {
    const { allMarkdownRemark } = this.props.data
    const allTags = R.flatten(
      allMarkdownRemark.edges.map(edge => edge.node.frontmatter.tags)
    )

    const allUniqTags = R.uniq(allTags)
    const tagCounts = R.countBy(t => R.toLower(t), allTags)
    this.maxCount = R.pipe(R.values, R.reduce((a, b) => R.max(a, b), 0))(tagCounts)

    this.tags = R.pipe(
      R.map(tag => ({
        name: tag,
        count: tagCounts[R.toLower(tag)],
      })),
      R.sort((tagA, tagB) => tagB.count - tagA.count)
    )(allUniqTags)
  }

  render() {
    const siteMetadata = this.props.data.site.siteMetadata
    const title = `포스트 태그 · ${siteMetadata.title}`

    return (
      <PageWrapper>
        <Helmet
          title={title}
          link={[
            {
              rel: 'canonical',
              href: `${siteMetadata.url}${this.props.location.pathname}`,
            },
          ]}
          meta={[
            {
              property: 'og:url',
              content: `${siteMetadata.url}${this.props.location.pathname}`,
            },
            { property: 'og:title', content: title },
          ]}
        />
        <NavBar />
        <PostContentWrapper>
          <Title>Tag</Title>
          <TagsTreepmap
            tags={this.tags}
            history={this.props.history}
            maxCount={this.maxCount}
          />
        </PostContentWrapper>
        <Footer />
      </PageWrapper>
    )
  }
}

export default TagsRoute

export const tagsQuery = graphql`
  query TagQuery {
    site {
      siteMetadata {
        title
        url
      }
    }
    allMarkdownRemark(limit: 2000) {
      edges {
        node {
          frontmatter {
            tags
          }
        }
      }
    }
  }
`



